import {useRef,useState} from 'react';
import classes from'./CheckedOut.module.css';


const isEntervalid =value=>value.trim()==='';
const isValidNumber =value=>value.trim().length===10;


const CheckedOut=(props)=>{
    
    const[formInputIsvalidate,setFormInputIsValidate]=useState({
        name:true,
        address:true,
        city:true,
        number:true
    });

    const nameInputRef=useRef();
    const addressInputRef=useRef();
    const cityInputRef=useRef();
    const numberInputRef=useRef();

   
    const buttonHandler=(event)=>{
        event.preventDefault();

        const enteredName= nameInputRef.current.value;
        const enteredAddress= addressInputRef.current.value;
        const enteredCity=cityInputRef.current.value;
        const enteredNumber=numberInputRef.current.value;
    
    
        const nameIsValid=!isEntervalid(enteredName);
        const addressIsValid=!isEntervalid(enteredAddress);
        const cityIsValid=!isEntervalid(enteredCity);
        const numberIsValid=isValidNumber(enteredNumber);

        setFormInputIsValidate({
            name:nameIsValid,
            address:addressIsValid,
            city:cityIsValid,
            number:numberIsValid
        });
        const formIsValid =
        nameIsValid &&
        addressIsValid &&
        cityIsValid &&
        numberIsValid;
  
      if (!formIsValid) {
        return;

        }
        props.onsubmitted({
            name:enteredName,
            address:enteredAddress,
            city:enteredCity,
            number:enteredNumber
        })
    
    };
    const nameError=`${classes.control} ${formInputIsvalidate.name?'': classes.invalid}`;
    const addressError= `${classes.control} ${formInputIsvalidate.address?'': classes.invalid}`;
    const cityError= `${classes.control} ${formInputIsvalidate.city?'': classes.invalid}`;
    const numberError= `${classes.control} ${formInputIsvalidate.number?'': classes.invalid}`;
    

  return(<form onSubmit={buttonHandler }>
        <div className={nameError}>
            <label htmlFor='name'>Name</label>
            <input type='text'id='name' ref={nameInputRef}/>
            {!formInputIsvalidate.name&&<p>please enter correct name</p>}
        </div>
        <div className={addressError}>
            <label htmlFor='address'>Address</label>
            <input type='text'id='address' ref={addressInputRef}/>
           {!formInputIsvalidate.address&& <p>please enter correct address</p>}
            </div>
        <div className={cityError}>
        <label htmlFor='city'>City</label>
            <input type='text'id='city'ref={cityInputRef}/>
            {!formInputIsvalidate.city&&<p>please enter correct city</p>}
        </div>
        <div className={numberError}>
        <label htmlFor='number'>Number</label>
            <input type='text'id='number'ref={numberInputRef}/>
           {!formInputIsvalidate.number&& <p>please enter correct number</p>}
        </div >
        <div className={classes.actions}>
        <button type='button' className={classes.action} onClick={props.onCancel}>Cancel</button>
        <button className={classes.submit} >Confirm</button>
        </div>
        </form>)
};
export default CheckedOut;