import React, { useContext,useState } from 'react';
import CheckedOut from './CheckedOut';
import Modal from '../UI/Modal';
import CartItem from './CartItem';
import classes from './Cart.module.css';
import CartContext from '../../store/cart-context';


const Cart = (props) => {

  const[ischeckedOut,setCheckedOut]=useState(false);
  const[isSubmitting, SetSubmitting]=useState(false);
  const[didSubmit, SetDidSubmit]=useState(false);
  const cartCtx = useContext(CartContext);
  const orderHandler=()=>{
    setCheckedOut(true);
  };


  const totalAmount = `$${cartCtx.totalAmount.toFixed(2)}`;
  const hasItems = cartCtx.items.length > 0;

  const cartItemRemoveHandler = (id) => {
    cartCtx.removeItem(id);
  };

  const cartItemAddHandler = (item) => {
    cartCtx.addItem({ ...item, amount: 1 });
  };
  const submitHandlerok=async(userData)=>{
    SetSubmitting(true);
    await fetch('https://manihs-ae75a-default-rtdb.firebaseio.com/order.json',{
    method:'POST',
    body:JSON.stringify({
      user:userData,
      orderItems:cartCtx.items
  })
});
SetSubmitting(false);
SetDidSubmit(true);
cartCtx.clearCart();
  };
 
 
  const cartItems = (
    <ul className={classes['cart-items']}>
      {cartCtx.items.map((item) => (
        <CartItem
          key={item.id}
          name={item.name}
          amount={item.amount}
          price={item.price}
          onRemove={cartItemRemoveHandler.bind(null, item.id)}
          onAdd={cartItemAddHandler.bind(null, item)}
        />
      ))}
    </ul>


    
  );
  const buttonHide=<div className={classes.actions}>
      <button className={classes['button--alt']} onClick={props.onClose}>
        Close
      </button>
      {hasItems && <button className={classes.button} onClick={orderHandler}>Order</button>}
    </div>;
   const cartModalContent=<React.Fragment>{cartItems}
   <div className={classes.total}>
     <span>Total Amount</span>
    
     <span>{totalAmount}</span>
   </div>
   {ischeckedOut&&<CheckedOut onsubmitted={submitHandlerok} onCancel={props.onClose} />}
   {!ischeckedOut&&buttonHide}</React.Fragment>
   const isSubmittingModalContent =<p></p>
   const didSubmitModalContent=
   <React.Fragment>
    <p>Your Order Have Been Placed</p>
    
  <div className={classes.actions}>
      <button className={classes['button--alt']} onClick={props.onClose}>
        Close
      </button>
      
    </div>
      
    
   </React.Fragment>

  return (
    <Modal onClose={props.onClose}>
     {!isSubmitting&&!didSubmit&&cartModalContent}
     {isSubmitting&&isSubmittingModalContent}
     {!isSubmitting&&didSubmit&&didSubmitModalContent}

    </Modal>
  );
};

export default Cart;